import { Component, OnInit } from '@angular/core';
import { User,AssetJourneyRequestParams } from '../../models';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute,Router} from '@angular/router';
import { HomeService, CommonService} from '../../services';


@Component({
  selector: 'app-completed-courses',
  templateUrl: './completed-courses.component.html',
  styleUrls: ['./completed-courses.component.css']
})
export class CompletedCoursesComponent implements OnInit {
  courses:[{asset:{title:"",awardPoints:0,contentType:""}}];
  loadHide:Boolean=false;
  error:Boolean=false;
  clients: any[];
  url:String="";
  assetUserJourney:[{
    id:0,
    assetCompletionStatus:null,
    asset:{id:0,title:"",fileName:"",description:"",mandatory:false},
    assetAssignmentDate:Date,
    dueDays:0,
	  ssetDueDate:"",
	  pontsRewarded:0
  }]
  count:number=0;
  queryUrl:String="";
  assetsTypeId:string="";
  currentUser: User;
  constructor(private route: ActivatedRoute,private router: Router,private homeService: HomeService,private commonService:CommonService) {  
    this.currentUser = JSON.parse(localStorage.getItem('currentUser')); 
    
this.route.queryParams.subscribe(params => {
  this.assetsTypeId=params['assetsTypeId']
  this.url=params['url'];
  
  this.homeService.getCompletedAssets(params['userId'],params['assetsTypeId'],params['productId'],params['disId'],params['taId'])
    .pipe()
     .subscribe(
         data => {
          this.courses=data.data;
        
         },
         error => {
         
         });

})
  }

   openAsset(assetUserJourney:any){
   // alert("ggggg");
    this.loadHide=true;
    this.error=false;
    
    //console.log("journy id: " + assetUserJourney.id)
    if (assetUserJourney.asset.contentType.id == 8) {
      this.route.queryParams.subscribe(params => {
        this.router.navigate(['/quiz/assets'], {
          queryParams: {
            "url": assetUserJourney.asset.assetType,
            "userId": params['userId'],
            "quizId": assetUserJourney.asset.fileName,
            "journeyId": assetUserJourney.id,
            "queryUrl": params['queryUrl'],
            "assetsTypeId": params['assetsTypeId'],
            "productId": params['productId'],
            "disId": params['disId'],
            "taId": params['taId']
          }, skipLocationChange: true
        });
        
      });

    

    }
    else {
      //alert("22");
       this.homeService.openAsset(this.currentUser.id, assetUserJourney.id)
        .pipe()
        .subscribe(content => {
          if (navigator.appVersion.toString().indexOf('.NET') > 0) { // for IE browser
            var blob = new Blob([content], { type: content.type });
            window.navigator.msSaveBlob(blob, assetUserJourney.asset.fileName);
          } else {
            var blob = new Blob([content], { type: content.type }),
              url = window.URL.createObjectURL(blob);
            window.open(url);
          }
           this.route.queryParams.subscribe(params => {

              this.homeService.getOrderedAssetsFor(new AssetJourneyRequestParams(Number(params['userId']), Number(params['assetsTypeId']), Number(params['taId']), Number(params['productId']), Number(params['disId'])))
              .pipe()
              .subscribe(

                data => {
                 
                   this.route.paramMap.subscribe(params => {
                    // this.showNotCompleted=false;
                    this.assetUserJourney = data.data;
                    if (Number(this.assetUserJourney.length) >= 1) {
                      //this.showNotCompleted=true;
                    }
                    this.count = Number(this.assetUserJourney.length);
                    // alert(this.count);
                    this.loadHide=false;

                  },
                    error => {
                      this.loadHide=false;  
                      this.error=true;
                    });
                },
                error => {
                
                  this.loadHide=false;
                  this.error=true;
                }
                
                );
          }
          );
        },
        error => {
          this.loadHide=false;
          this.error=true;
        }
       
        );
    }
   
  //  console.log(status); 
  }

  goBack(urls:String){   
    this.route.queryParams.subscribe(params => {  
      this.router.navigate([urls], { queryParams:  {
        "userId":this.currentUser.id,
        "queryUrl":params['queryUrl'],
        "assetsTypeId":params['assetsTypeId'],
        "productId":params['productId'],
        "disId":params['disId'],
        "taId":params['taId']


      }, skipLocationChange: true});
    })
  }

  ngOnInit() {
    
  }

}
